:root {
    --blue-color: #0c2f54;
    --dark-color: #0d0f11;
    --white-color: #fff;
}

/* text-colors */
.text-dark {
    color: var(--dark-color);
}

.text-blue {
    color: var(--blue-color);
}

.text-end {
    text-align: right;
}

.text-center {
    text-align: center;
}

.text-start {
    text-align: left;
}

.text-bold {
    font-weight: 800;
}

/* hr line */

.hr {
    height: 1px;
    background-color: rgba(0, 0, 0, 0.1);
}

/* border-bottom */

.border-bottom {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.invoice-wrapper {
    min-height: 100vh;
    background-color: rgba(0, 0, 0, 0.1);
    padding-top: 20px;
    padding-bottom: 20px;
}

.invoice {
    max-width: 850px;
    margin-right: auto;
    margin-left: auto;
    background-color: #fff;
    padding: 70px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 5px;

}

.invoice li {
    list-style: none;
}

.invoice-head {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.invoice-head-top-left img {
    width: 20px;

}

.invoice img{
    width: 100px;
}

.invoice-head-top-right h3 {
    font-weight: 500;
    font-size: 27px;
    color: var(--blue-color);
}

.invoice-head-bottom-left {
    display: flex;
    justify-content: space-between;
    padding: 1rem 0;
}

.invoice-head-middle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
}

.invoice-head-bottom {
    padding: 16px 0;
}

.invoice-body {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    overflow: hidden;
}

.invoice-body table {
    border-collapse: collapse;
    border-radius: 4px;
    width: 100%;
}

.invoice-body table td,
.invoice-body table th {
    padding: 9px;
}

.invoice-body table tr {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.invoice-body table thead {
    background-color: rgba(0, 0, 0, 0.02);
}

.invoice-body-info-item {
    display: grid;
    grid-template-columns: 80% 20%;
    padding: 5px 0;
}

.invoice-body-item .info-item-td {
    padding: 12px;
    background-color: rgba(0, 0, 0, 0.02);
}

.invoice-foot {
    padding: 30px 0;
}

.invoice-foot p {
    font-size: 12px;
}

.invoice-btns {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 1rem;
}





.invoice-head-bottom {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 10px;
}

@media screen and (max-width: 992px) {
    .invoice {
        padding: 40px;
    }
}

@media screen and (max-width: 576px) {



    .invoice-head-bottom {
        grid-template-columns: repeat(1, 1fr);
    }

    .invoice-head-bottom-right {
        margin-top: 12px;
        margin-bottom: 12px;
    }

    .invoice * {
        text-align: left;
    }

    .invoice {
        padding: 28px;
    }
}



.invoice-body {
    min-width: 600px;
}



/* CSS */



@media print {
    body {

        padding: 0;
        margin: 0.2cm;

    }

    button {
        display: none;
    }

}

@page {
    size: A4;
    margin: 0;
}